.App {
  margin-left: auto;
  margin-right: auto;
  width: 90%;
  border-radius: 1em;
  max-width: 1000px;
  min-width: 350px;
  font-family: "Roboto", sans-serif; }
  .App h1 {
    text-align: center;
    color: #43b581;
    font-family: 'Roboto Mono', monospace; }

body {
  background-color: #202225;
  color: #d1d4c9; }

input {
  background-color: #2f3136;
  border: 1px solid #000000;
  color: #d1d4c9;
  padding: 3px; }

textarea {
  background-color: #2f3136;
  border: 1px solid #000000;
  color: #d1d4c9;
  padding: 7px;
  height: 10em;
  width: 100%;
  border-radius: 3px;
  font-family: "Roboto Mono", monospace;
  margin-top: 2em; }

.KeyInput {
  margin: 2px auto;
  display: table; }
  .KeyInput .gridLayout {
    display: grid;
    grid-template-columns: -webkit-max-content -webkit-max-content;
    grid-template-columns: max-content max-content;
    grid-gap: 5px;
    align-items: center; }
    .KeyInput .gridLayout label {
      text-align: right;
      color: #43b581; }

.AlphabetDisplay {
  display: grid;
  grid-auto-flow: column;
  overflow-x: auto;
  max-width: 725px;
  margin: 0 auto;
  padding-top: 1em; }
  .AlphabetDisplay p {
    padding: 0 5px;
    margin: 0;
    height: 1.5em;
    font-family: "Source Code Pro", monospace; }
  .AlphabetDisplay .AlphabetDisplayHeader {
    margin-right: 5px;
    text-align: right;
    justify-content: center;
    font-weight: bold;
    color: #00b0f4; }
  .AlphabetDisplay .AlphabetDisplayLetter {
    text-align: center; }

.CipherSelector {
  display: grid;
  margin: 2em auto;
  align-items: center; }
  .CipherSelector .Cipher {
    grid-column: 1;
    margin: 0 auto; }
  .CipherSelector .Direction {
    grid-column: 2;
    margin: 0 auto; }
  .CipherSelector .PreserveFormatting {
    grid-column: 3;
    margin: 0 auto; }
  .CipherSelector select {
    background-color: #2f3136;
    border: 1px solid #000000;
    color: #d1d4c9;
    padding: 3px; }
  .CipherSelector option {
    background-color: #2f3136;
    border: 1px solid #000000;
    color: #d1d4c9;
    padding: 3px; }

.MascSection {
  display: grid;
  grid-auto-flow: column;
  overflow-x: auto;
  max-width: 750px;
  margin: 0 auto; }
  .MascSection .header {
    margin-right: 5px;
    text-align: right;
    justify-content: center;
    font-weight: bold; }
  .MascSection p {
    margin: 2px 10px 4px 0; }
  .MascSection .ciphertextLabel {
    color: #43b581; }
  .MascSection .plaintextLabel {
    color: #00b0f4; }
  .MascSection .KeyLetter {
    max-width: 30px;
    padding-right: 1px; }
    .MascSection .KeyLetter input {
      margin: 0 2px;
      width: 1em;
      text-align: center;
      display: block;
      margin: 0 auto; }
    .MascSection .KeyLetter label {
      padding: 1px 2px;
      font-family: "Source Code Pro", monospace;
      text-align: center;
      display: block; }

.CaesarSection {
  margin: 2px auto;
  display: flex;
  justify-content: center; }
  .CaesarSection label {
    text-align: right;
    padding: 2px 4px;
    color: #43b581; }
  .CaesarSection input {
    margin: 0 2px;
    width: 3em; }

.Credits {
  color: #6d6d6d;
  font-size: smaller;
  font-family: 'Roboto', sans-serif;
  text-align: right; }
  .Credits .name {
    color: #00b0f4; }
    .Credits .name:visited {
      color: #00b0f4; }
  .Credits .course {
    font-style: italic; }

